import {
  CHANGE_LIST_VIEW,
  CHANGE_THUM_VIEW,
  HD_SW_UPLOAD_PANEL,
  RESET_UPLOAD_MSG  
} from '../mutation-types'

const state = {
  bView: false,
  B_upload_panel: false
}

const getters = {
  // 
  getBview: state => state.bView,
  
  // 
  getB_upload_panel: state => state.B_upload_panel
}
const actions = {
 
}
const mutations = {
  // 
  [ CHANGE_LIST_VIEW ] (state) {
    state.bView = false
  },

  // 
  [ CHANGE_THUM_VIEW ] (state) {
    state.bView = true
  },
  // /
  [ HD_SW_UPLOAD_PANEL ] (state) {
    state.B_upload_panel = !state.B_upload_panel
  }  
}

export default {
  state,
  getters,
  actions,
  mutations
}


